<template>
	<view>
		<uni-card padding="0" spacing="0" :title="item.shop.title" :sub-title="item.price + '积分'"
			:thumbnail="'http://192.168.5.2/dev-api'+item.shop.img" v-for="(item,index) in list">
			<template v-slot:cover>
				<view class="custom-cover">

					<view class="cover-content"  style="padding-top: 10px;color: #909399;margin-left: 16rpx;font-size: 20rpx;">
						<text class="uni-subtitle uni-white">订单号：{{item.orderNumber}}</text>
						<text style="margin-left: 50rpx;">
							{{item.createTime}}
						</text>
					</view>
				</view>
			</template>
			<view slot="actions" class="card-actions no-border">


				<view class="card-actions-item" @click="actionsClick(item)">
					<uni-icons type="chatbubble" size="18" color="#999"></uni-icons>
					<text class="card-actions-item-text">{{item.comment ? '已评价':'待评价'}}</text>
				</view>
				<view class="card-actions-item" @click = "goViewVideo(item)">
					<uni-icons type="heart" size="18" color="#999"></uni-icons>
					<text class="card-actions-item-text">查看课程</text>
				</view>
			</view>
		</uni-card>
		<view>
			<!-- 输入框示例 -->
			<uni-popup ref="inputDialog" type="dialog">
				<uni-popup-dialog ref="inputClose" mode="input" title="输入评价内容" value="" placeholder="请输入内容"
					@confirm="dialogInputConfirm"></uni-popup-dialog>
			</uni-popup>
		</view>
		<view>
			<!-- 提示信息弹窗 -->
			<uni-popup ref="alertDialog" type="dialog">
				<uni-popup-dialog :type="msgType" cancelText="关闭" confirmText="确定" :title="msgType"
					:content="messageText" @confirm="dialogConfirm" @close="dialogClose"></uni-popup-dialog>
			</uni-popup>
		</view>
	</view>
</template>

<script>
	var graceJS = require('@/GraceUI5/js/grace.js');
	var request = require('@/GraceUI5/js/request/request.js');
	export default {
		data() {
			return {
				avatar: 'https://qiniu-web-assets.dcloud.net.cn/unidoc/zh/unicloudlogo.png',
				list: [],
				order: {},
				msgType: "",
				messageText: ""
			}
		},
		onLoad() {
			this.getOrderList()
		},
		methods: {
			getOrderList() {
				uni.gRequest.get("bohe/getOrderList", {
					data: {
						userId: graceJS.getStorage("id")
					}
				}, (res) => {
					console.log(res)
					this.list = res.data
				})
			},
			//打开评价窗口
			actionsClick(e) {
				this.order = e
				this.$refs.inputDialog.open()
			},
			//提交评价内容
			dialogInputConfirm(val) {
				console.log(val)
				uni.gRequest.post("bohe/createShopComment", {
					data: {
						userId: graceJS.getStorage("id"),
						shopId: this.order.shopId,
						orderId: this.order.id,
						userName: graceJS.getStorage("name"),
						content: val
					}
				}, (res) => {
					console.log(res)
					if (res.data.code == 500) {
						this.msgType = "error"

					} else {
						this.msgType = "error"

					}
					this.messageText = res.data.msg

					this.$refs.alertDialog.open()
					this.getOrderList()
				})
			},
			goViewVideo(e){
				uni.navigateTo({
					url:"./viewVideo?id=" + e.shopId
				})
			}
		}
	}
</script>

<style>
	.card-actions {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		height: 45px;
		border-top: 1px #eee solid;
	}

	.card-actions-item {
		display: flex;
		flex-direction: row;
		align-items: center;
	}

	.card-actions-item-text {
		font-size: 12px;
		color: #666;
		margin-left: 5px;
	}

	.cover-image {
		flex: 1;
		height: 150px;
	}

	.no-border {
		border-width: 0;
	}
</style>